/* --- Keyframe Animations --- */
@keyframes fluid-background {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* --- Base Styles --- */
.page {
  display: flex;
  /* This flex container will now center the .content div,
     as .controls is taken out of the normal flow. */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  font-family: var(--font-sans-serif);
  color: var(--color-text);

  /* The star of the show: the animated fluid background */
  background: linear-gradient(
    -45deg,
    var(--color-bg-gradient-1),
    var(--color-bg-gradient-2),
    var(--color-bg-gradient-3),
    var(--color-bg-gradient-4)
  );
  background-size: 400% 400%;
  animation: fluid-background 15s ease infinite;
}

/* --- Content Card --- */
.content {
  /* This is now the only element in the normal flow inside .page,
     so it will be perfectly centered. No changes needed. */
  background: var(--color-foreground);
  border-radius: calc(var(--spacing-unit) * 1.5);
  padding: calc(var(--spacing-unit) * 3);
  border: 1px solid var(--color-foreground-border);
  box-shadow: var(--shadow-soft);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px); /* For Safari */
  text-align: center;
  width: 90%;
  max-width: 500px;
  animation: fade-in-up 0.7s ease-out forwards;
  font-family: var(--font-monospace);
  font-size: 1.5rem;
  font-weight: bold;
}

/* --- Controls & Buttons --- */
.controls {
  /* Fixed position to stick to the top */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;

  /* Layout for the buttons inside */
  display: flex;
  justify-content: center;
  gap: var(--spacing-unit);

  /* Spacing and glassy appearance */
  padding: calc(var(--spacing-unit) * 1.5);
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.button {
  font-family: var(--font-monospace);
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  color: var(--color-accent);
  background-color: transparent;
  border: 2px solid var(--color-accent);
  padding: calc(var(--spacing-unit) * 0.75) calc(var(--spacing-unit) * 1.5);
  border-radius: var(--spacing-unit);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition:
    color 0.4s ease-out,
    transform 0.2s ease;
  z-index: 1;
}

.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-accent);
  transform: translateX(-100%);
  transition: transform 0.4s ease-out;
  z-index: -1;
}

.button:hover {
  color: var(--color-accent-text);
  transform: translateY(-2px);
}

.button:hover::before {
  transform: translateX(0);
}

.button:active {
  transform: translateY(0px) scale(0.98);
}
