/* --- CSS Variables --- */
:root {
  /* Light Mode Palette */
  --color-bg-gradient-1: #e0c3fc;
  --color-bg-gradient-2: #8ec5fc;
  --color-bg-gradient-3: #f6d365;
  --color-bg-gradient-4: #fda085;
  --color-foreground: rgba(255, 255, 255, 0.75);
  --color-foreground-border: rgba(255, 255, 255, 0.9);
  --color-text: #2c3e50;
  --color-accent: #0077ff;
  --color-accent-text: #ffffff;

  /* Typography */
  --font-sans-serif:
    -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  --font-monospace:
    "SFMono-Regular", Consolas, "Liberation Mono", Menlo, monospace;

  /* Spacing & Shadows */
  --spacing-unit: 1rem;
  --shadow-soft: 0 8px 32px 0 rgba(31, 38, 135, 0.17);
  --shadow-lifted: 0 12px 40px 0 rgba(31, 38, 135, 0.25);
}
